import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component{
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts.mx/api/v2/list_movies.json");
    this.setState({ movies, isLoading: false })
  };
  // async! javascript야 얘를 좀 기다려! 함수 내에서는 await을 보고 axios 기다려야 하는구나?
  // javascript가 axios 끝나고 나서 자기 할 일을 마저 함
  componentDidMount(){
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return (<section className="container">
      {isLoading 
      ? (<div className="loader">
        <span className="loader__text">Loading...</span>
      </div>)
      : (
        <div className="movies">
          {movies.map(movie => (
            <Movie
              key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres} />
          ))}
        </div>
      )}
      </section>
      );
  }
}

//App이 render
//처음에는 isLoading? true
//app이 mount되고 나면 componentDidMount 호출(=getMovies function)호출
//getMovies는 axios.get사용하는데 시간이 필요해서 await 들어가 있음

export default App;